.sidebar {
  width: 30%; /* Adjust the width of the sidebar as needed */
  height: 100%; /* Make the sidebar full height */
  background-color: #000000; /* Background color of the sidebar */
  color: hsl(0, 0%, 100%); /* Text color */
  position: fixed; /* Fix the sidebar position */
  top: 0; /* Position from the top */
  left: 0; /* Position from the left */
  justify-content: center;
  align-items: center;
  .Image img {
    width: 70%;
    height: auto;
    padding-top: 30%;
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
  }

  .contactID {
    display: flex;
    padding-top: 10%;
    padding-bottom: 10%;
    list-style: none;
    left: calc(50% - 50px);
    transform: translateX(29%);
    .link {
      width: 80%;
      height: auto;
    }
    .git {
      width: 80%;
      height: auto;
    }
  }

  .Button {
    display: flex;
    justify-content: center;  /*Horizontally center the button */
    /*align-items: center; /* Vertically center the button */
    .button {
      background-color: transparent;
      border: 2px solid #ffffff; /* Set border width and color */
      color: #ffffff; /* Set text color */
      padding: 10px 20px; /* Adjust padding as needed */
      font-size: 16px; /* Set font size */
      cursor: pointer;
      border-radius: 9px;
      text-align: center;
    }
    .button:hover {
      border: 4px solid aquamarine;
      background-color: transparent; /* Change background color on hover */
      color: aquamarine; /* Change text color on hover */
      font-size: 20px;
      transition: transform 0.3s ease-in-out;
    }
  }
}
