body {
  background-color: #000000;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
}

.wrapper {
  padding-top: 10%;
  display: flex;
  justify-content: space-around;
  gap: 5px;
}

.container {
  width: 20vmin;
  height: 20vmin;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 1em 0;
  position: relative;
  background-color: #000000;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
  border-radius: 15px;
  text-align: center;
  transition: transform 0.3s ease-in-out;
  
  .num {
    color: aquamarine;
    font-weight: 600;
    font-size: 2.5em;
  }
  
  .text {
    color: #ffffff;
    font-size: 1.2em;
  
    font-weight: 600;
    text-transform: uppercase;
  }
}

.container:hover {
  transform: scale(1.05);
}



@media screen and (max-width: 1024px) {
  .wrapper {
    width: 90vw;
    flex-wrap: wrap;
    gap: 20px;
  }

  .container {
    width: calc(33.33% - 20px);
    height: 350px;
    font-size: 1.4em;
  }
}

@media screen and (max-width: 768px) {
  .wrapper {
    width: 90vw;
    flex-wrap: wrap;
    gap: 20px;
  }

  .container {
    width: calc(50% - 20px);
    height: 350px;
    font-size: 1.6em;
  }
}

@media screen and (max-width: 480px) {
  .wrapper {
    gap: 15px;
  }

  .container {
    width: 100%;
    height: 350px;
    font-size: 1.4em;
  }
}
