.Text {
  .h4 {
    color: aquamarine;
    font-size: 20pt;
    margin-top: 10%;
  }

  .p {
    color: grey;
    font-size: 14pt;
  }

  .Hori {
    align-items: start;
    font-size: 20pt;
    justify-content: start;
    .dd {
      justify-content: start;
      color: grey;
      left: 100%;
      margin-top: 5%;
      font-size: 15pt;
      transform: translateX(-5%);
    }
  }

  .Services {
    display: flex;
    list-style: none;
    justify-content: space-around;
    gap: 5px;
    .buu {
      background-color: transparent;
      border: 2px solid #ffffff; /* Set border width and color */
      color: #ffffff; /* Set text color */
      padding: 10px 15px; /* Adjust padding as needed */
      font-size: 18px; /* Set font size */
      cursor: pointer;
      border-radius: 20px;
      text-align: center;
      transition: transform 0.3s ease-in-out;
      width: 200px;
      height: 80px;
    }

    .buu:hover {
      border: 4px solid aquamarine;
      background-color: transparent; /* Change background color on hover */
      color: aquamarine; /* Change text color on hover */
      font-size: 20px;
    }
  }

  .Skills {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    justify-content: start;
    overflow: hidden;
    .DLogo {
      width: auto;
      height: 75px;
      padding: 15px;
      filter: grayscale(1);
      transition: all  350ms ease;
    }

    .DLogo:hover{
      filter: grayscale(0);
      transform: scale(1.05);
    }
  }

  .h2 {
    font-size: 35pt;
  }
}
