.projects-con {
  -webkit-column-count: 3;
  -moz-column-count: 3;
  column-count: 3;
  -webkit-column-width: 33%;
  -moz-column-width: 33%;
  column-width: 33%;
  padding: 0 12px;

  .pro {
    -webkit-transition: all 350ms ease;
    transition: all 350ms ease;
    cursor: pointer;
    margin-bottom: 15px;
    padding-left: 0;
    padding-right: 0;
    padding-bottom: 0;
    width: 100%;
    gap: 5px;
    border-color: aquamarine;
    overflow: hidden;
    
  }

  
  .contentss {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    opacity: 0;
    transition: all  350ms ease;
  }
  .contentss:hover{
    opacity:1;
    
  }
}

.Title {
  color: aquamarine;
  font-size: 20pt;
  margin-top: 10%;
}

.imgWraper{
    position: relative;
    overflow: hidden;
}

.imgWraper:hover .pro{
    transform: scale(1.05);
}